$title:20px;
$primary-color:#0089b7;
$secondary-color: lightgray;

$gap-big:20px;
$gap-sm:10px;


.navbar{
    height: 100px;
    background-color: $secondary-color;
    display: flex;
    align-items: center;
    margin: 0px 250px;

        .titleCon{
            flex:3;
            background-color: $primary-color;
            height: 100%;
            color: black;

            .subTitleCon{
                padding-left: 20px;
                height: 100%;
                gap: $gap-sm;
                display: flex;
                flex-direction: column;

                .title{
                    font-size: 40px;
                    font-family: 'Raleway', sans-serif;
                    letter-spacing: 1.2rem;
                }
    
                .subTitle{
                    font-size: 24px;
                    letter-spacing: 1rem;
                    font-family: 'Lato', sans-serif;
                    font-style: italic;
                    font-weight: 300;
                }
            }

        }
        
        .imageCon{
            flex:1;
            height: 100%;

            img{
                width: 100px;
                height: 100px;
                border-radius: 50%;
                object-fit: cover;
                margin-left: 50px;
            }
        }
    
}

.contact{

    display: flex;
    flex-direction: column;
    gap: $gap-sm;
    padding: 10px;
    color: $secondary-color;
    font-size: 14px;

    .contactCon{
        display: flex;
        align-items: center;
        gap: $gap-big;


        .icon{
            width: 14px;
            height: 14px;
            svg{
                fill: $secondary-color;
            }
                
        }

        a:hover{
            
                color: white !important; 
                cursor: pointer;
            
        }
    }
}

.skills{
    padding: 10px;
    color: $secondary-color;


    .title{
        font-size: $title;
        letter-spacing: 0.5rem;
        margin-bottom: 15px;
        font-weight: bold;
    }

    .itemsCon{
        gap: $gap-sm;
        display: flex;
        flex-direction: column;

       div{
        display: list-item;
        list-style: inside;
        font-size: 13px;
       }
    }
}

.projects{
    padding: 20px 0px 10px 20px;
    color:$primary-color;
    gap:$gap-big;

    .title{
        font-size: $title;
        margin: 20px 0px;
        letter-spacing: 0.5rem;
        border-radius: 10px;
        font-weight: bold;

        
    }

    .mainCon{
        display: flex;
        flex-direction: column;
        gap: $gap-big;

        .container{
            display: flex;
            flex-direction: column;
            gap: $gap-sm;
            
            .projectTitle{
                font-size: 18px;
                font-weight: bold;
                letter-spacing: 0.2rem;
                &:hover{
                    color: aqua !important; 
                    cursor: pointer;
                }
            }
            
            .properties{
                font-style: italic;
                font-weight: 300;
                letter-spacing: 0.2rem;
                line-height: 1.1rem;
            }
        }
    }
}

.education{
    display: flex;
    flex: 1;
    flex-direction: column;
    gap: $gap-sm;
    padding: 10px;
    color: $secondary-color;

    .title{
        font-size: $title;
        letter-spacing: 0.5rem;
        font-weight: bold;
    }
    .mainCon{
        display: flex;
        flex-direction: column;
        gap: $gap-sm;

        .container{
            display: flex;
            flex-direction: column;

            .uni{
                font-weight: bold;
                font-size: 14px;
            }
            .dep{
                font-size: 13px;
            }
            .year{
                font-style: italic;
                font-size: 13px;
            }
        }
    }
}

.experience{
    display: flex;
    flex:2;
    flex-direction: column;
    gap: $gap-big;
    color:$primary-color;
    padding: 40px 0px 0px 20px;

    .title{
        font-size: $title;
        letter-spacing: 0.5rem;
        font-weight: bold;
    }

    .container{
        display: flex;
        flex-direction: column;
        gap:$gap-sm;

        span{
            font-weight: bold;
        }
    }
}

